"use client";

// src/app/page.jsx
import Link from "next/link";
import { useEffect, useState } from "react";

import { Button } from "@/components/ui/button";
import {
  DUMMY_OPENID_PROVIDER,
  OPENID_SESSION_KEY,
} from "@/features/auth/openid/constants";
import { loadStoredSession } from "@/features/auth/openid/openIdClient";

export default function Page() {
  const [session, setSession] = useState(() => loadStoredSession());

  useEffect(() => {
    if (typeof window === "undefined") {
      return undefined;
    }

    const handleStorage = (event) => {
      if (event.storageArea && event.storageArea !== sessionStorage) {
        return;
      }

      if (event.key && event.key !== OPENID_SESSION_KEY) {
        return;
      }

      setSession(loadStoredSession());
    };

    window.addEventListener("storage", handleStorage);
    return () => window.removeEventListener("storage", handleStorage);
  }, []);

  return (
    <section className="relative h-[600px] overflow-hidden rounded-xl border shadow-sm">
      <div
        aria-hidden="true"
        className="absolute inset-0 bg-[url('/images/Wow-gif.gif')] bg-cover bg-center"
      />
      <div
        aria-hidden="true"
        className="absolute inset-0 bg-white/70 backdrop-blur-sm dark:bg-slate-950/70"
      />
      <div className="relative z-10 grid gap-6 p-6">
        <div className="grid gap-4">
          <h1 className="text-3xl font-semibold tracking-tight">Welcome</h1>
          <p className="text-base text-muted-foreground">
            더미 OpenID SSO 로그인을 통해 데모 계정으로 대시보드를 체험할 수
            있습니다. 폐쇄망 이전 시 실제 IdP 엔드포인트를 연결하면 동일한 흐름을
            재사용할 수 있습니다.
          </p>
        </div>

        {session ? (
          <div className="max-w-md rounded-lg border bg-background/90 p-4 shadow-sm">
            <p className="font-medium">현재 로그인된 사용자</p>
            <ul className="mt-2 space-y-1 text-sm text-muted-foreground">
              <li>이름: {session.user?.name}</li>
              <li>이메일: {session.user?.email}</li>
              <li>토큰 만료: {session.tokens?.expiresAt}</li>
            </ul>
            <p className="mt-3 text-xs text-muted-foreground">
              실제 OpenID 프로바이더와 연동 시 토큰 저장 로직만 교체하면 됩니다.
            </p>
          </div>
        ) : (
          <div className="flex max-w-md flex-col gap-3 rounded-lg border border-dashed bg-background/90 p-4 shadow-sm">
            <p className="text-sm text-muted-foreground">
              아직 로그인되지 않았습니다. 아래 버튼을 눌러
              {" "}
              {DUMMY_OPENID_PROVIDER.name}에 연결하세요.
            </p>
            <Button asChild size="lg" className="w-fit">
              <Link href="/login">OpenID 로그인 페이지로 이동</Link>
            </Button>
          </div>
        )}
      </div>
    </section>
  );
}
